@mixin pin($top, $right, $bottom, $left)
    top: $top
    right: $right
    bottom: $bottom
    left: $left

@mixin flex($justify, $align, $direction)
    display: flex
    justify-content: $justify
    align-items: $align
    flex-direction: $direction

@mixin family($name, $weight)
    font-family: $name, sans-serif
    font-weight: $weight
    line-height: 180%

@mixin centerX
    margin:
        right: auto
        left: auto

@mixin centerY
    margin:
        top: auto
        bottom: auto

@mixin icon($utf)
    font-family: "icons", sans-serif
    display: inline-flex
    justify-content: center
    align-items: center
    width: 24px
    height: 24px


    &::before
        content: $utf
        speak: none
        font-size: 100%
        font-style: normal
        font-weight: normal
        font-variant: normal
        text-transform: none
        -webkit-font-smoothing: antialiased
        -moz-osx-font-smoothing: grayscale
        padding:
            left: 1px
            right: 1px

@mixin noVisible($x,$y,$z)
    opacity: 0
    transform: translate($x, $y) scale($z)
    will-change: auto
    transition: opacity 800ms cubic-bezier(0.165, 0.84, 0.44, 1), transform 800ms cubic-bezier(0.165, 0.84, 0.44, 1)




