.aproyect
  background-color: $color__white
  transition: background-color .4s cubic-bezier(0.165, 0.84, 0.44, 1)
  &__container
    & > article
      padding: 0

  &__landing
    text-align: center
    &--anyo
      margin-top: 5rem
    &--title
      margin-top: 5rem
    &--summary
      margin-top: 5rem
    &--row
      margin-top: 5rem
    &--tags
      @include flex(center,initial,flex)
      margin-top: 1rem
      &-tag
        font-size: .75rem
        text-transform: uppercase
        padding: 0.25rem 0.625rem
        letter-spacing: .03125rem
        background-color: $color__blue--light
        line-height: initial
        margin-right: .5rem
        &:hover
          color: $color__blue--hover
  &__resumen

    margin-top: 3rem
    margin-bottom: 3rem
    &--firtsrow, &--secondrow, &--thirdrow
      @include flex(initial, initial, column)
      & > div
        & > h5
          margin-bottom: 1rem
          margin-top: 3rem
      & > div
        width: 100%
    &--tool
      width: 7rem

  &__flex
    @include flex(initial,initial,column)
  &__cover
    &--photo
      max-height: 629px
      object-fit: cover
  &__content
    padding: 0
    & > h2
      @include family("CY", 500)
      font-size: 46px
      letter-spacing: -.05px
      @media (max-width: 900px)
          font-size: 23px
      margin:  3.5rem 0 0 0
      max-width: 636px
      text-align: center
      @include centerX

    & > p
      @include family("Maxx", 400)
      font-size: 20px
      letter-spacing: 0
      margin:  3.5rem 0
      & > text
        max-width: 636px
      & > img
        max-height: 629px
        object-fit: cover
        max-width: 100%
  &__resto
    position: relative
    &--container
      min-height: 347px
      box-sizing: border-box
      left: -10vw
      right: -10vw
      padding: 5rem 0
    &--proyects
      margin-top: 1rem
      @include flex(initial, initial,row)
      flex-wrap: wrap

.aiframes
    width: 100%
    max-height: 20rem
    background-color: #e6f2f9
    border-radius: 0.5rem
    margin-top: 3rem
    padding: 1rem
    box-sizing: border-box