.article
  padding: 3rem 0 0 0
  position: relative
  &__container
    display: grid
    grid-template-columns: 20% 80%
  &__title
    color: $color--blue
    margin-left: 4rem
    z-index: 1
    font-size: 26px
  &__day
    text-align: center
    @include flex(center,center,row)
    color: $color__grey
  &__img
   display: none


