.header
    width: 100%
    opacity: 1
    transition: background-color .4s cubic-bezier(0.165, 0.84, 0.44, 1)

    &__container
        margin: 0 auto
    &__logo
        padding: 2rem .5rem
        top: 0
        z-index: 10
        width: 100%
        box-sizing: border-box
        overflow: hidden
        &--img
            background-repeat: no-repeat
            background-image: url(../images/ruben-moya-logo.svg)
            background-position: left center
            background-size: 100% 100%
            @include transition-fast
            padding: 5% 29%
            display: block
            & > span
                position: absolute
                overflow: hidden
                height: 1px
                width: 1px
                padding: 0
                border: 0
                top: 50%

    &__menu
        padding: 6px 1rem
        bottom: 0
        background-color: $color__white
        position: fixed
        height: 56px
        width: 100%
        box-sizing: border-box
        box-shadow: $shadow__main
        display: flex
        align-items: center
        z-index: 11
        &--ul
            display: grid
            grid-template-columns: repeat(4, 1fr)
            height: 100%
            width: 100%
            & > li
                height: 100%
                width: 100%
                & > a
                    @include flex(center,center,row)
                    text-transform: uppercase
                    height: 100%
                    width: 100%
                    font-size: 1rem

