.headline1
    @include family("CY", 500)
    font-size: 58px
    letter-spacing: -0.15px
    @media (max-width: 900px)
        font-size: 33px

.headline2
    @include family("CY", 500)
    font-size: 46px
    letter-spacing: -.05px
    @media (max-width: 900px)
        font-size: 28px

.headline3
    @include family("CY", 400)
    font-size: 33px
    letter-spacing: 0
    @media (max-width: 900px)
        font-size: 20px

.headline4
    @include family("CY", 400)
    font-size: 23px
    letter-spacing: 0.25px
    @media (max-width: 900px)
        font-size: 16px

.headline5
    @include family("CY", 500)
    font-size: 14px
    letter-spacing: 0


.body
    @include family("Maxx", 400)
    font-size: 20px
    letter-spacing: 0



.text-light
    font-weight: 300

.text-semi-bold
    font-weight: 500

.text-bold
    font-weight: 600

.text-white
    color: $color__white

.text-error
    color: $color__red
    font-weight: 400
    font-size: .75rem
    line-height: 125%

.text-red
    color: $color__red

.text-italic
    font-style: italic

.text-uppercase
    text-transform: uppercase

.text-underline
    text-decoration: underline

.text-capitalize
    text-transform: capitalize
