.ipresentation
  &__title
    margin: 3rem 0

.iphoto
  &__img
    aspect-ratio: 1 / 1
    object-fit: cover

.idescription
  background-color: $color__yellow
  &__container
    padding-top: 13rem
    padding-bottom: 5rem
    margin: -8rem auto 0 auto
  &__description

    &--text

      & > p
        margin-bottom: 2rem
    &--links
      @include flex(space-between, flex-start, row)

      width: 100%
      & > li
        & > a
          margin: 0
          & > i
            margin: 0
.ipowers
  &__container
    margin-top: 5rem
    margin-bottom: 2rem
    display: grid
    grid-template-columns: 50% 50%

  &__title
    width: 100%
    grid-column: 1 / 3
    margin-bottom: 1rem
    & > h5
      margin: 0
  &__first-column
    width: 100%
    grid-column: 1 / 2
    margin-right: .5rem
    &--title
      margin-bottom: 24px
    & > div
      margin-bottom: 40px
      & > ul
        & > li
          font-size: 14px
  &__second-column
    width: 100%
    grid-column: 2 / 3
    margin-left: .5rem
    &--long
      max-width: 419px
    &--title
      margin-bottom: 24px
    & > div
      margin-bottom: 40px
      & > ul
        & > li
          font-size: 14px
.inews
  background-color: $color__blue--light
  &__container
    padding-bottom: 3rem
  &__texts
    padding-top: 3rem
    padding-bottom: 3rem
    display: grid
    grid-template-columns: 50% 50%
    &--label
      margin-bottom: 2rem
    & > div
      & > ul
        & > li
          font-size: 14px
  &__photos
    padding: 4rem 0
    display: initial
    @media only screen and (min-width: 543px)
      display: grid
      grid-template-columns: 1fr 1fr 1fr
      column-gap: 1rem
    &--ind , &--par
      border-radius: .5rem
      aspect-ratio: 4 / 3
      object-fit: cover
      width: 100%
    &--par
      margin: .5rem 0