.link__simple
    color: $color__blue
    @include transition-fast
    font-weight: 500
    &:hover
      color: $color__blue--hover
      font-weight: 600
.link__button
    color: $color__blue
    @include transition-fast
    font-weight: 500
    border: 1px solid $color__blue
    border-radius: 2rem
    padding: 1.125rem 1.5rem
    margin: 2rem 0
    display: block
    text-align: center
    width: fit-content
    text-transform: uppercase
    &:hover
        color: $color__blue--hover
        font-weight: 600
        border: 1px solid $color__blue--hover

.link__active
  color: $color__blue--hover
  font-weight: 600