.footer
  background-color: $color__footer
  height: 640px
  &__container
    @include flex(space-between,center,column)
    height: 100%
    padding: 80px 0
    box-sizing: border-box
  &__eyes, &__social
    @include flex(center,center,row)
    text-align: center
    & > li:first-child
      margin-right: 1rem
  &__title
    @include flex(center,center,column)
    text-align: center