// =======
// Global variables
// =======

// COLORS
$color__white: #FFFFFF
$color__black: #111b27

$color__blue: #223655
$color__blue--hover: #F8BB01
$color__footer: #F5F6FD
$color__blue--light: #e6f2f9

$color__green: #E6F9EA
$color__yellow: #FAF9E7

$color__grey: #A8B0B4
$color__grey--light: #effad6


$color__red: #F76161
$color__light-red: #FFDBDB

// WIDHT
$widht__container--xl: 1296px
$widht__container--l: 1076px
$widht__container--m: 856px

// SIZES
// padding
$padding__xxl: 10rem
$padding__xl: 6rem
$padding__l: 4rem
$padding__m: 2rem
$padding__s: 1.5rem
$padding__xs: 1rem
$padding__xxs: .75rem
$padding__xxxs: .5rem
// space between
$gap__xxxl: 6rem
$gap__xxl: 4rem
$gap__xl: 2rem
$gap__l: 1.5rem
$gap__m: 1rem
$gap__s: .75rem
$gap__xs: .5rem
$gap__xxs: .25rem
// element sizes
$el-size__queen: 25rem
$el-size__xxxl: 15rem
$el-size__xxl: 10rem
$el-size__xl: 4.5rem
$el-size__l: 3.75rem
$el-size__m: 3rem
$el-size__s: 2.25rem
$el-size__xs: 1.5rem
$el-size__xxs: 1.25rem
$el-size__xxxs: .75rem
// button sizes
$button-size__m: 3.25rem
$button-size__s: 2.75rem
// input height
$search-height: 4.25rem
// carousel element sizes
$swiper-item__m: 21rem
// button sizes
$button-password: 2.75rem

// BORDERS
$border__main: 1px solid
$border__thick: 2px solid

// SHADOWS
$shadow__main: 0 -1px 6px #00000029
$shadow__main-hidden: 0 0 2rem rgba($color__black, 0)
$shadow__main-inset: inset .2rem .2rem .4rem rgba($color__black, .25)
$shadow__main-inset-dark: inset .2rem .2rem .4rem rgba($color__black, 1)
$shadow__small-inset: inset .1rem .1rem .25rem rgba($color__black, .25)
$shadow__small-inset-dark: inset .1rem .1rem .25rem rgba($color__black, 1)

// TRANSITIONS
$transition__main: .3s ease-in-out
$transition__slow: .5s ease-in-out

// BREAKPOINTS
$common-breakpoint__s: 500px
