@keyframes bounceUp
    0%
        transform: translateY(0)
        opacity: 1
    25%
        transform: translateY($padding__m)
    100%
        transform: translateY(-7rem)
        opacity: 0

@keyframes bounceFromDown
    0%
        transform: translateY(7rem)
        opacity: 0
    75%
        transform: translateY(-$padding__m)
    100%
        transform: translateY(0)
        opacity: 1

@keyframes bounceToDown
    0%
        transform: translateY(0)
        opacity: 1
    25%
        transform: translateY(-$padding__m)
    100%
        transform: translateY(7rem)
        opacity: 0

@keyframes slideDown
    0%
        transform: translateY(-7rem)
        opacity: 0
    100%
        transform: translateY(0)
        opacity: 1

.rodar
    animation: rodar 0.8s infinite linear
    display: inline-flex
    height: 36px
    @keyframes rodar
        0%
             transform: rotateZ(0deg)
        100%
             transform: rotateZ(360deg)

