.hvideo
  &__video
    margin: 0
    &--mesure
      width: 100%
      aspect-ratio: 9 / 16
      object-fit: cover
  &__eyes
    margin: 3rem 0
    @include flex(center,center,row)
    & > h4
      & > span.rodar
        height: 24px !important
.hvalue
  &__container
    padding-top: 3rem
    padding-bottom: 3rem
    display: grid
    grid-template-columns: 1fr
  &__text
    & > h3
      margin-bottom: 1rem
    margin-right: 1rem
  &__icons
    margin-top: 3rem
    display: grid
    grid-template-areas: "a a a b b b" "c c d d e e"
    & > img
      grid-area: auto/ span 2
    &--logos
      &-firts
        grid-area: a !important
      &-second
        grid-area: b !important
.hproyects
  &__articles
    display: initial
    @media only screen and (min-width: 543px)
      display: grid
      grid-template-columns: 1fr 1fr
      grid-column-gap: 1rem
    & > div.proyect
      margin-top: 2rem
      & > a
        & > img.proyect__img
            width: 100%
            aspect-ratio: 1 / 1
            object-fit: cover
  &__link
    display: block
    box-sizing: border-box
    margin: 3rem 0
    width: fit-content
.hpresentation
  background-color: $color__blue--light
  &__container
    padding-bottom: 3rem
  &__grid
    padding-top: 5rem
    display: grid
    grid-template-columns: 1fr
    &--text
      & > a
        margin: 3rem 0
        display: block
    &--dates
      display: grid
      width: 100%
      grid-template-columns: 1fr 1fr
      & > div:first-child
        & > h4
          width: 6rem
      & > a.link__button
        display: flex
        align-items: baseline
  &__photos
    display: initial

    @media only screen and (min-width: 543px)
      display: grid
      grid-template-columns: 1fr 1fr 1fr
      column-gap: 1rem

    &--ind , &--par
      border-radius: .5rem
      aspect-ratio: 4 / 3
      object-fit: cover
      width: 100%
    &--par
      margin: .5rem 0
.harticles
  margin: 6rem 0
  &__link
    margin-top: 5rem

