.tag-menu
  &__container
    margin: 1rem 0 3rem 0
    box-sizing: border-box
  &__title
    margin-bottom: 1rem
.tagcloud
  display: flex
  flex-wrap: wrap
  & > li
    display: flex
.items
  &__container
    display: flex
    flex-wrap: wrap
    justify-content: space-between
    margin-bottom: 6rem
    @media only screen and (min-width: 543px)
      display: grid
      grid-template-columns: 1fr 1fr
      grid-column-gap: 1rem


   